//  Link         .
import { Link } from "react-router-dom";
import ArrowImg from "../assets/Arrow.png";
import PagesImg from "../assets/Pages.png";
import styled from "styled-components";
import Seohyun from "../assets/Seohyun.png";
import Somi from "../assets/Somi.png";
import Eunseo from "../assets/Eunseo.png";
import Chaeyoung from "../assets/Chaeyoung.png";

const Container = styled.div`
  //  css
  background: linear-gradient(
      0deg,
      rgba(76, 157, 88, 0.21875) 60.56%,
      rgba(73, 147, 83, 0) 71.74%
    ),
    #61d271;
  padding-bottom: 20vh;
  overflow: hidden;
`;
const Title = styled.h1`
  letter-spacing: -0.5vw;
  font-family: Inter;
  font-style: normal;
  font-weight: 900;
  font-size: 12vw;
  color: #ffffff;
  margin: 10vh 6vw;
  padding: 1vw;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black,
    2px 2px 4px grey;
`;
const Arrow = styled.img`
  //   css

  width: 20vw;
  height: auto;
  margin: 3vw 6vw;
`;
const Detail = styled.p`
  //  css
  letter-spacing: -0.15vw;
  color: white;
  padding: 1vw;

  margin: 10vh 6vw;
  font-family: "Inter";
  font-style: normal;
  font-weight: 200;
  font-size: 2vw;
  line-height: 5vh;
`;
const Pages = styled.img`
  margin-top: -15vh;
  width: 100%;
  height: 30vh;
`;
const White = styled.div`
  background-color: #fafafa;
  margin-top: -1vh;
  padding-bottom: 10vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;
const MText = styled.div`
  margin: 3vw;
  h3 {
    font-family: "Inter";
    font-weight: 900;
    font-size: 10vh;
    margin: 4.5vh 3vw;
    letter-spacing: -0.3vh;
    line-height: 10vh;
  }
  p {
    text-align: center;
    font-family: "Inter";
    font-style: normal;
    font-weight: 700;
    font-size: 2vh;
    line-height: 27px;

    color: #00e063;
  }
`;
const Members = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;
const Member = styled.div`
  margin: 1vw;
  img {
    width: 23vh;
    margin-top: 7vh;
  }
  h1 {
    text-align: center;
    font-family: "Inter";
    font-weight: 900;
    font-size: 3vw;
    margin: 4.5vh 1vw;
    letter-spacing: -0.3vh;
    line-height: 1vw;
  }
  p {
    text-align: center;
    font-family: "Inter";
    font-style: normal;
    font-weight: 300;
    font-size: 3vh;
    line-height: 27px;

    color: #00e063;
  }
`;
function Main() {
  return (
    <>
      <Container>
        <Title>
            <br /> 
          <Link to={{ pathname: `/Name/*` }}>
            <Arrow src={ArrowImg} />
          </Link>
        </Title>

        <Detail>
             Knapsack     
          <br />
                  .
        </Detail>
      </Container>

      <Pages src={PagesImg} />
      <White>
        <MText>
          <h3>
            People
            <br />
            baking
            <br />
            Cookie
          </h3>
          <p>   .</p>
        </MText>
        <Members>
          <Member>
            <img src={Seohyun} alt="Seohyun" />
            <h1></h1>
            <p> /</p>
          </Member>
          <Member>
            <img src={Somi} alt="Somi" />
            <h1></h1>
            <p>UX/UI</p>
          </Member>
          <Member>
            <img src={Eunseo} alt="Eunseo" />
            <h1></h1>
            <p>/ </p>
          </Member>
          <Member>
            <img src={Chaeyoung} alt="Chaeyoung" />
            <h1></h1>
            <p> </p>
          </Member>
        </Members>
      </White>
    </>
  );
}

export default Main;
